import * as types from './mutation-types'

const mutations = {
    [types.SET_LOGINPAGE](state, isLoginPage) {
        state.isLoginPage = isLoginPage
    },
    [types.SET_AUTOLOGIN](state, isAutoLogin) {
        state.isAutoLogin = isAutoLogin
    },
    [types.SET_CURRENT_PRODUCT](state, currentProduct) {
        state.currentProduct = currentProduct
    },
    [types.SET_SHOPPINGCART_ITEMS](state, shoppingcartItems) {
        state.shoppingCartItems = shoppingcartItems
    }
}

export default mutations


