import * as types from './mutation-types'
import { setAutoLoginFlag, setProduct, setShoppingCartItems} from 'common/js/cache'

export const setLoginPage = function({commit}, loginPage) {
    commit(types.SET_LOGINPAGE, loginPage)
}

export const setAutoLogin = function({commit}, isAutoLogin) {
    commit(types.SET_AUTOLOGIN, setAutoLoginFlag(isAutoLogin))
}

// 这里采用localStorgae的原因是，避免页面刷新后vuex数据消失
export const setCurrentProduct = function({commit}, currentProduct) {
    commit(types.SET_CURRENT_PRODUCT, setProduct(currentProduct))
}

export const setShoppingCart = function ({ commit }, shoppingCartItems) {
    commit(types.SET_SHOPPINGCART_ITEMS, setShoppingCartItems(shoppingCartItems))
}


