import storage from 'good-storage'

const AUTOLOGIN_KEY = '__AUTOLOGIN__'
const USERINFO_KEY = '__USERINFO__'
const PRODUCT_KEY = '__PRODUCT__'
const SHOPPINGCART_KEY = '__SHOPPINGCART__'

// 登录页面flag
export function setAutoLoginFlag(flag) {
    storage.set(AUTOLOGIN_KEY, flag)
}
export function getAutoLoginFlag() {
    return storage.get(AUTOLOGIN_KEY, false)
}

// 用户信息
export function getUserInfo(){
    return storage.get(USERINFO_KEY, '')
}
export function setUserInfo(obj){
    storage.set(USERINFO_KEY, obj)
}
export function removeUserInfo(){
    storage.remove(USERINFO_KEY)
}

// 当前商品
export function getProduct(){
    return storage.get(PRODUCT_KEY, {})
}
export function setProduct(obj){
    storage.set(PRODUCT_KEY, obj)
    return obj
}

// 当前购物车的商品
export function getShoppingCartItems() {
    return storage.get(SHOPPINGCART_KEY, {})
}
export function setShoppingCartItems(obj) {
    storage.set(SHOPPINGCART_KEY, obj)
    return obj
}


